<template>
	<div class="page2_3_3">
		<div class="page-top">
			<Place class="fl" :data="msg"/>
			<div class="fr inter">
				<interBtn title1="绩效审计" title="历史比较疑点" id="more2_3_3" ydname="审计疑点线索挖掘"/>
			</div>
		</div>
		<div>
			<div class="lineBox box-border-shadow">
				<div v-if="showThy" id="lishiLine" class="line"></div>
				<img style="display:block;margin: 20px auto;" v-else src="../../../assets/no-sj.png">
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Place from './place'
	import interBtn from './../interactions'
	import * as echarts from 'echarts'
	import reqData from "../../../reqData.js"
	export default{
		name:'page2_3_3',
		data(){
			return{
				msg:["审计疑点线索挖掘","绩效审计","与历史水平比较"],
				showThy:true
			}
		},
		components:{
			Place,
			interBtn
		},
		computed:{
			companyID(){
				return JSON.parse(this.$store.getters.getCompany).id;
			},
			getWord(){
				return this.$store.getters.getWord;
			},
			getWord1(){
				return this.$store.getters.getWord1;
			}
			
		},
		methods:{
			optionLishiLine(data1,data2){
				data1=data1[0];
				if(data2){
					data2=data2[0];
					return {
		                title: {
		                    text: data1.text+" -- "+data2.text+"（参照指标） / 历史数据",
		                    textStyle: {
		                        color:'#333',
		                        fontSize:22
		                    }
		                },
		                grid:{
		                    y:145,
		                    y2:20,
		                },
		                tooltip: {
		                    trigger: 'axis'
		                },
		                legend: {
		                    data:[data1.text,data2.text],
		                    top:60
		                },
		                toolbox: {
		                    show: true,
		                    feature: {
		                        dataZoom: {
		                            yAxisIndex: 'none'
		                        },
		                        dataView: {readOnly: false},
		                        magicType: {type: ['line', 'bar']},
		                        restore: {},
		                        saveAsImage: {}
		                    }
		                },
		                xAxis:  {
		                    type: 'category',
		                    boundaryGap: false,
		                    data: data1.time
		                },
		                yAxis: [
		                    {
		                        name: data1.text + "(" + data1.danwei + ")",
		                        type: 'value',
		                        boundaryGap: [0.1, 0.1],
		                        scale: true,
		                        axisLabel: {
		                            formatter: '{value} ' + data1.danwei
		                        }
		                    },
		                    {
		                        name: data2.text + "(" + data2.danwei + ")",
		                        type: 'value',
		                        boundaryGap: [0.1, 0.1],
		                        scale: true,
		                        nameLocation: 'start',
		                        inverse: true,
		                        axisLabel: {
		                            formatter: '{value} ' + data2.danwei
		                        }
		                    }
		                ],
		                series: [
		                    {
		                        name:data1.text,
		                        type:'line',
		                        smooth: true,
		                        data:data1.value,
		                        lineStyle: {
		                            normal: {
		                                width: 3,
		                                shadowColor: 'rgba(0,0,0,0.4)',
		                                shadowBlur: 10,
		                                shadowOffsetX: 4,
		                                shadowOffsetY: 10

		                            }
		                        }
		                    },
		                    {
		                        name:data2.text,
		                        type:'line',
		                        yAxisIndex:1,
		                        smooth: true,
		                        data:data2.value,
		                        lineStyle: {
		                            normal: {
		                                width: 3,
		                                shadowColor: 'rgba(0,0,0,0.4)',
		                                shadowBlur: 10,
		                                shadowOffsetX: 4,
		                                shadowOffsetY: 10

		                            }
		                        },
		                        markPoint: {
		                            data: data2.yiidan
		                        },
		                    }
		                ]
		            }
				};
				return {
			        title: {
		                text: data1.text+"/历史对比",
		                textStyle: {
		                    color:'#333',
		                    fontSize:22
		                }
		            },
		            grid:{
			            y:145,
			            y2:20,
			        },
		            tooltip: {
		                trigger: 'axis'
		            },
		            legend: {
		                data:[data1.text],
		                top:60
		            },
		            toolbox: {
		                show: true,
		                feature: {
		                    dataZoom: {
		                        yAxisIndex: 'none'
		                    },
		                    dataView: {readOnly: false},
		                    magicType: {type: ['line', 'bar']},
		                    restore: {},
		                    saveAsImage: {}
		                }
		            },
		            xAxis:  {
		                type: 'category',
		                boundaryGap: false,
		                data: data1.time
		            },
		            yAxis: {
		            	name: data1.text + "(" + data1.danwei + ")",
		                type: 'value',
		                boundaryGap:[0.1,0.1],
		                scale:true,
		                axisLabel: {
		                    formatter: '{value} '+data1.danwei
		                }
		            },
		            series: [
		                {
		                    name:data1.text,
		                    type:'line',
		                    smooth: true,
		                    data:data1.value,
		                    lineStyle: {
		                        normal: {
		                            width: 3,
		                            shadowColor: 'rgba(0,0,0,0.4)',
		                            shadowBlur: 10,
		                            shadowOffsetX: 4,
		                            shadowOffsetY: 10

		                        }
		                    },
		                    markPoint: {
		                        data: data1.yiidan
		                    },
		                }
		            ]   
			    };
			},
			getLishiLine(){
				var myLishiLine = echarts.init(document.getElementById('lishiLine'));
				myLishiLine.showLoading();
				let _this=this;
				reqData
			      .req({
			        apiName: "lishiData",
			        data: {             
			          name:this.getWord1[0],
			          ID:this.companyID
			        }
			      })
			      .then(res => {
			        res = res.data;
			        if (res && res.code === "200") {
			          let data1 = res.data;
			          console.log(data1);
			          if(data1.length==0){
			          		_this.showThy=false
			          }else{
			          	  if(_this.getWord1.length>1){
			          		reqData
						      .req({
						        apiName: "lishiData",
						        data: {             
						          name:_this.getWord1[1],
						          ID:this.companyID
						        }
						      })
						      .then(res => {
						        res = res.data;
						        if (res && res.code === "200") {
						          let data2 = res.data;
						          console.log(data2);
						          myLishiLine.hideLoading();
						          myLishiLine.setOption(this.optionLishiLine(data1,data2));  
						          sessionStorage.setItem("lishiData", [JSON.stringify(data1),JSON.stringify(data2)]);
						        }
						      })
						      .catch(err => {
						        console.log(err);
						      });
				          }else{
				          	myLishiLine.hideLoading();
				         	 myLishiLine.setOption(this.optionLishiLine(data1));  
				          	sessionStorage.setItem("lishiData", JSON.stringify(data1));
				          }
			          }
			          
			        }
			      })
			      .catch(err => {
			        console.log(err);
			      });
			 	  $(window).resize(function(){
						myLishiLine.resize({
							width:'auto',
							height:'auto'
						})
				  });
			}
		},
		mounted: function(){
			this.getLishiLine();
		}
	}
</script>
<style type="text/css" scoped="">
	.lineBox{
		padding: 3% 5%;
		box-sizing: border-box;
	    width:100%;
	    height: 800px;
	    margin: 20px auto;
	}
	.lineBox .line{
		display: block; 
		width: 100%;
		height: 100%;
		margin: 0 auto;
	}
</style>


